<template>
  <div class="header__card">
    <ButtonBack @click="router.back()"/>
    <div class="basket__card">
      <div class="card__chosen-products">
        <span class="card__count-products">{{count}} шт</span>
        <span class="card__sum-products">на сумму {{summa}} ₽</span>
      </div>
      <router-link to="/Basket">
        <BasketButton/>
      </router-link>
      <router-link to="/Auth">
      <ButtonExit/>
    </router-link>
    </div>
  </div>
</template>

<script>
import { computed } from 'vue'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'
import ButtonBack from '@/components/ui/ButtonBack.vue'
import BasketButton from '@/components/ui/BasketButton'
import ButtonExit from '@/components/ui/ButtonExit.vue'
export default {
  name: 'HeaderCard',
  components: {
    ButtonBack,
    BasketButton,
    ButtonExit
  },
  props: {
  },
  setup () {
    const store = useStore()
    const router = useRouter()
    const count = computed(() => {
      return store.getters.getCount
    })
    const summa = computed(() => {
      return store.getters.getSumma
    })
    return {
      count,
      summa,
      router
    }
  }
}
</script>

