<template>
  <div class="container">
    <div v-if="showLogin" class="login">
      <form class="form__log">
        <a @click="showLogin = false" class="link__reg">
          Зарегистрироваться
        </a>
        <h2 class="title">вход</h2>
        <input v-model="loginUsername" class="input" type="text" placeholder="Логин" required>
        <span v-if="errorMessage" class="error">{{ errorMessage }}</span>
        <input  v-model="loginPassword" class="input" type="password" placeholder="Пароль" required>
        <span v-if="errorMessage" class="error">{{ errorMessage }}</span>
        <div class="checkbox">
          <input class="custom-checkbox" type="checkbox" id="checkbox-one" name="updates" value="yes">
          <label  for="checkbox-one">  Я согласен получать обновления на почту  </label>
      </div>
      <button class="submit" @click.prevent="login">Войти</button>
    </form>
    </div>
    <div v-else class="registr">
      <form class="form__log">
        <a @click="showLogin = true" class="link__reg">
          Авторизоваться
        </a>
        <h2 class="title">Регистрация</h2>
        <input v-model="regUsername" class="input" type="text" placeholder="Логин">
        <span v-if="errorMessage" class="error">{{ errorMessageReg }}</span>
        <span v-if="errorMessage" class="error">{{ errorMessageRegTwo }}</span>
        <input v-model="regPassword" class="input" type="password" placeholder="Пароль">
        <span v-if="errorMessage" class="error">{{ errorMessageReg }}</span>
        <span v-if="errorMessage" class="error">{{ errorMessageRegTwo }}</span>
        <div class="checkbox">
          <input class="custom-checkbox" type="checkbox" id="checkbox-one" name="updates" value="yes">
          <label  for="checkbox-one">  Я согласен получать обновления на почту  </label>
        </div>
        <button class="submit"  @click.prevent="register">Зарегистрироваться</button>
      </form>
    </div>
  </div>
</template>

<script>
import { ref } from 'vue'
import { useRouter } from 'vue-router'
export default {
  setup () {
    const showLogin = ref(true)
    const router = useRouter()
    const loginUsername = ref('')
    const loginPassword = ref('')
    const regUsername = ref('')
    const regPassword = ref('')
    const errorMessage = ref('')
    const errorMessageReg = ref('')
    const errorMessageRegTwo = ref('')
    const login = () => {
      if (loginUsername.value === '' || loginPassword.value === '') {
        errorMessage.value = 'Поле не должно быть пустым'
      } else if (loginUsername.value.length >= 4 || loginPassword.value.length >= 4) {
        const users = JSON.parse(localStorage.getItem('users')) || []
        const foundUser = users.find(user => user.username === loginUsername.value && user.password === loginPassword.value)
        if (foundUser) {
          router.push('/')
        } else {
          alert('пользователь еще не зарегистрирован')
        }
      } else {
        alert('некорректно введены данные')
      }
    }
    const register = () => {
      if (regUsername.value === '' || regPassword.value === '') {
        errorMessageReg.value = 'Поле не должно быть пустым'
      } else if (regUsername.value.length < 4 || regPassword.value.length < 4) {
        errorMessageRegTwo.value = 'Логин и пароль должны содержать не менее 4-х символов'
      } else {
        const users = JSON.parse(localStorage.getItem('users')) || []
        const foundUser = users.find((user) => user.username === regUsername.value)
        if (foundUser) {
          alert('такой пользователь уже зарегистрирован')
        } else {
          const newUser = {
            username: regUsername.value,
            password: regPassword.value
          }
          users.push(newUser)
          localStorage.setItem('users', JSON.stringify(users))
          alert('вы успешно зарегестрировались, пройдите авторизацию')
        }
      }
    }
    return {
      showLogin,
      loginUsername,
      loginPassword,
      regUsername,
      regPassword,
      errorMessage,
      errorMessageReg,
      errorMessageRegTwo,
      login,
      register
    }
  }
}
</script>

<style lang="scss" scoped>
  .container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-image: url('../assets/background2.png');
  background-repeat: no-repeat;
  background-size: cover;
}
.form__log {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding: 7px 20px;
  width: 460px;
  height: 340px;
  background-color: #fff;
}
.input {
  padding: 10px 0 10px 18px;
  border-radius: 61px;
  border: 1px solid #D58C51;
  &::placeholder {
    color: #161516;
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }
}
.custom-checkbox {
  position: absolute;
  z-index: -1;
  opacity: 0;
}
.link__reg {
  cursor: pointer;
  color: #D58C51;
  text-align: right;
  font-family: Montserrat;
  font-size: 11px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
  text-decoration-line: underline;
}
.title {
  text-align:center;
  color: #161516;
  font-family: 'Montserrat',sans-serif;
  font-size: 31px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  text-transform: uppercase;
}
.submit {
  cursor: pointer;
  margin-left: 100px;
  width: 200px;
  height: 42px;
  color: #131313;
  font-family: 'Montserrat',sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  letter-spacing: 1px;
  background-color: #D58C51;
  border: 1px solid #D58C51;
}
.custom-checkbox+label {
  display:flex;
  align-items: center;
  margin-right: 10px;
  margin-bottom: 25px;
}
.custom-checkbox+label::before {
  content: '';
  width: 18px;
  height: 18px;
  border: 1px solid #D58C51;
  border-radius: 45px;
  margin-right: 9px;
}
.custom-checkbox:checked+label::before {
  background-color: #D58C51;
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
}

.custom-checkbox:disabled+label::before,.custom__checkbox-two:disabled+label::before {
  background-color: #D58C51;
}
.label {
  color: #301411;
  font-family: 'Montserrat',sans-serif;
  font-size: 11px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
}
.error {
  color: #FF0B0B;
  font-family: 'Montserrat', sans-serif;
  font-size: 10px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
}
</style>
