<template>
  <div class="header">
    <h1 class="header__title">наша продукция</h1>
    <div class="basket">
      <div class="chosen-products">
        <span class="count-products">{{count}} шт</span>
        <span class="sum-products">на сумму {{summa}} ₽</span>
      </div>
      <router-link to="/Basket">
        <BasketButton/>
      </router-link>
      <router-link to="/Auth">
      <ButtonExit/>
    </router-link>
    </div>
  </div>
</template>

<script>
import { computed } from 'vue'
import { useStore } from 'vuex'
import BasketButton from '@/components/ui/BasketButton'
import ButtonExit from '@/components/ui/ButtonExit.vue'
export default {
  name: 'HeaderMain',
  components: {
    BasketButton,
    ButtonExit
  },
  props: {
  },
  setup () {
    const store = useStore()
    const count = computed(() => {
      return store.getters.getCount
    })
    const summa = computed(() => {
      return store.getters.getSumma
    })
    return {
      count,
      summa
    }
  }
}
</script>

<style lang="scss" scoped>
  .header  {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 74px 77px 40px 77px;
    width: 100vw;
    height: 100px;
    background-color: #161516;
    &__title {
      font-family: 'Montserrat', sans-serif;
      font-size: 31px;
      font-weight: 700;
      line-height: 38px;
      text-transform: uppercase;
      letter-spacing: 0.1em;
      color: #FFF;
    }
  }
  .basket {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 25px;
    font-family: 'Montserrat', sans-serif;
    font-size: 17px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
  }
  .chosen-products {
    display: flex;
