<template>
  <div class="main__card">
    <main class="main">
    <img
    :src="currentCard.urlImg"
    alt="preview"
    class="card__image"
    >
  <div class="card">
    <h2 class="card__title">
      {{ currentCard.title }}
    </h2>
    <p
    class="card__description"
    >
    {{ currentCard.description }}
  </p>
    <div class="box">
      <p class="box__price"
      >{{ currentCard.price }} ₽
    </p>
    <ButtonInCard @clickAdd="addProducts(currentCard)"/>
      </div>
    </div>
  </main>

  </div>
</template>

<script>
import { useRouter, useRoute } from 'vue-router'
import { useStore } from 'vuex'
import { onMounted, computed } from 'vue'
import ButtonInCard from '@/components/ui/ButtonInCard.vue'
import { v4 as uuidv4 } from 'uuid'
export default {
  name: 'TheCard',
  components: {
    ButtonInCard
  },
  props: {
  },
  setup () {
    const router = useRouter()
    const route = useRoute()
    const store = useStore()
    onMounted(() => {
      console.log(route)
      console.log(store)
      store.commit('SetCurrentCard', route.params.id)
    })
    const currentCard = computed(() => {
      return store.getters.getCurrentCard
    })
    const addProducts = (item) => {
      const itemCardBasket = {
        id: item.id,
        idx: uuidv4(),
        urlImg: item.urlImg,
        title: item.title,
        price: item.price
      }
      store.commit('SetBasket', itemCardBasket)
    }
    return {
      currentCard,
      router,
      addProducts
    }
  }
}
</script>

<style lang="scss" scoped>
.main__card {
  color: #fff;
}
.main {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 200px;
    padding: 0 40px 200px 140px;
  }
  .card {
    display: flex;
    flex-direction: column;
    font-family: 'Montserrat', sans-serif;
    color: #FFF;
    &__image {
      width: 501px;
      height: 503px;
    }
    &__title {
      margin-bottom: 21px;
      color: #D58C51;
      font-family: 'Montserrat', sans-serif;
      font-size: 30px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
      width: 385px;
    }
    &__description {
      margin-bottom: 34px;
      width: 451px;
      color: #FFF;
      font-family: 'Montserrat',sans-serif;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 20px;
      text-align: left;
    }
  }
  .box {
    display: flex;
    flex-direction: row;
    justify-content: left;
    gap: 121px;
    align-items: center;
    &__price {
      color: #FFF;
      font-family: 'Montserrat',sans-serif;
      font-size: 23px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
    }
  }
</style>
