<template>
  <div class="basket__cards">
    <CardsOfBasket
    v-for="item in BasketProducts"
      :key="item.id"
      :urlImg="item.urlImg"
      :title="item.title"
      :price="item.price"
      @click="goToCard(item.id)"
    >
    <BasketDelete @clickRemove="removeProducts(item.idx)"/>
    </CardsOfBasket>
  </div>
  <div class="line">
  </div>
  </template>

<script>
import { computed } from 'vue'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'
import CardsOfBasket from '@/components/elements/CardsOfBasket'
import BasketDelete from '@/components/ui/ButtonDelete'
export default {
  name: 'BasketCards',
  components: {
    CardsOfBasket,
    BasketDelete
  },
  props: {
  },
  setup () {
    const store = useStore()
    const router = useRouter()
    const BasketProducts = computed(() => {
      return store.getters.getBasketProducts
    })
    const removeProducts = (productId) => {
      store.commit('SetRemoveBasket', productId)
    }
    const goToCard = (id) => {
      console.log(router)
      router.push(`/${id}`)
    }
    return {
      BasketProducts,
      removeProducts,
      goToCard
    }
  }
}
</script>

<style lang="scss" scoped>
  .basket__cards {
    display: flex;
    flex-direction: column;
    padding: 0 290px 100px 290px;
    gap: 31px;
  }

  .card {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .line {
    border-top: 1px solid #D58C51;
  }
</style>
