<template>
  <main class="main">
    <CardProducts
      @click="goToCard(item.id)"
      v-for="item in products"
      :key="item.id"
      :urlImg="item.urlImg"
      :title="item.title"
      :description="item.description"
      :price="item.price"
      >
      <TheButton @clickAdd="addProducts(item)"/>
    </CardProducts>
  </main>
  </template>

<script>
import { computed } from 'vue'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'

import { v4 as uuidv4 } from 'uuid'

import CardProducts from '@/components/elements/CardProducts'
import TheButton from '@/components/ui/TheButton'
export default {
  name: 'TheMain',
  components: {
    CardProducts,
    TheButton
  },
  props: {
  },
  setup () {
    const store = useStore()
    const router = useRouter()
    const products = computed(() => {
      return store.getters.getProducts
    })
    const addProducts = (item) => {
      const itemCardBasket = {
        id: item.id,
        idx: uuidv4(),
        urlImg: item.urlImg,
        title: item.title,
        price: item.price
      }
      store.commit('SetBasket', itemCardBasket)
    }
    const goToCard = (id) => {
      // console.log(router)
      router.push(`/${id}`)
    }
    return {
      products,
      addProducts,
      goToCard
    }
  }
}
</script>

<style lang="scss" scoped>
  .main {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 35px 15px;
    padding: 164px 74px;
    :hover  {
      color: #D58C51;
    }
  }
  .card {
    flex-direction: column;
    padding: 43px 17px 36px 17px;
    width: 310px;
    height: 550px;
    font-family: 'Montserrat', sans-serif;
    border: 1px solid rgba(213, 140, 81, 1);
    color: #FFF;
  }
</style>
