<template>
  <div class="footer">
    <div class="summa">
      Заказ на сумму:
      <span class="summa__total">
      {{totalSumma}} ₽
    </span>
  </div>
    <button @click="order(item)" class="button__submit">
    Оформить заказ
  </button>
  </div>
</template>

<script>
import { useRouter } from 'vue-router'
import { computed } from 'vue'
import { useStore } from 'vuex'

export default {
  name: 'TheFooter',
  components: {
  },
  props: {
  },
  setup () {
    const router = useRouter
    const store = useStore()
    const totalSumma = computed(() => {
      return store.getters.getTotalSumma
    })
    const order = (item) => {
      store.commit('SetDoOrder', item)
      // router.push('/main')
    }
    return {
      totalSumma,
      order,
      router
    }
  }
}
</script>

